$(window).resize(function () {
    resizeImg();
});

function resizeImg() {
    if ($(window).width() >= 768) {
        $('.desktop.previous-marathon li').height($('.desktop.previous-marathon li').width());
    }
}

function windowScroll() {
    var height = $(window).height();
    if ($(window).scrollTop() > height) {
        $('header nav').addClass('scrolling');
        $('.go-up').fadeIn();
    }
    else  {
        $('header nav').removeClass('scrolling');
        $('.go-up').fadeOut();
    }
}

$(document).ready(function() {
    windowScroll();
    resizeImg();

    $('.carousel').slick({
        dots: true,
        infinite: true,
        autoplay: true,
        autoplaySpeed: 4000,
        speed: 300,
        adaptiveHeight: true,
        slidesToShow: 1
    });

    $('.burger').on('click', function () {
        $(this, '.mobile-menu').toggleClass('open-menu');
        $('.overlay').fadeToggle();
        $('.mobile-menu').slideToggle();
    });

    $('.overlay').on('click', function () {
        $('.mobile-menu, .burger').removeClass('open-menu');
        $('.overlay').fadeOut();
        $('.mobile-menu').slideUp();
    });

    $(".scroll_to_target").on('click', function(e) {
        e.preventDefault();
        var target = $(this).attr('href');
        if ($(window).width() > 1023) {
            $('html, body').animate({scrollTop: $(target).offset().top - 92}, 1500);
        }
        else {
            $('html, body').animate({scrollTop: $(target).offset().top}, 1500);
        }
        return false;
    });

    $('.go-up').on('click', function () {
        $( 'html, body' ).animate( { scrollTop: 0 }, 1500 );
    });

    $('.carousel-slide').on('click', function (e) {
        e.preventDefault();
    });

    $(window).scroll(function() {
        windowScroll();
    });

    $('.slick-prev').html('<svg><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#slider-arrow"></use></svg>');
    $('.slick-next').html('<svg><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#slider-arrow"></use></svg>');

    $("form").submit(function() { //устанавливаем событие отправки для формы с id=form
        var form_data = $(this).serialize(); //собераем все данные из формы
        $.ajax({
            type: "POST", //Метод отправки
            url: "email_sender.php", //путь до php фаила отправителя
            data: form_data,
            success: function(responce) {
                alert("Ваши данные успешно отправлены!");
            }
        });
        return false;
    });

});

function myMap() {
    var myCenter = new google.maps.LatLng(48.449075, 35.057585);
    var mapCanvas = document.getElementById("map");
    var mapOptions = {
        center: myCenter,
        zoom: 15,
        scrollwheel: false
    };
    var map = new google.maps.Map(mapCanvas, mapOptions);
    var marker = new google.maps.Marker({position:myCenter});
    marker.setMap(map);

    var infowindow = new google.maps.InfoWindow({
        content: "Днепр, студия «Dzen», проспект Гагарина, 14"
    });
    infowindow.open(map,marker);
}
